import Vue from 'vue'
import VueRouter from 'vue-router'
import Tatuajes from '../views/Tatuajes.vue'

Vue.use(VueRouter)

const routes = [

  /******************************************************* 
  ************************** HOME ************************
  ********************************************************/

  {
    path: '/',
    name: 'tatuajes',
    component: Tatuajes
  },
  {
    path: '/tatuaje',
    name: 'tatuaje',
    component: () => import('../views/Tatuaje.vue')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/AboutView.vue')
  },

  /***********************************************************
  ************************** USUARIOS ************************
  ************************************************************/
  {
    path: '/login',
    name: 'login',
    component: () => import('../Login.vue')
  },
  {
    path: '/usuarios',
    name: 'usuarios',
    component: () => import('../views/Usuarios.vue')
  },
  {
    path: '/registro',
    name: 'registro',
    component: () => import('../views/Registro.vue')
  },
  {
    path: '/usuarios/editar',
    name: 'editarUsuario',
    component: () => import('../views/EditarUsuario.vue')
  },
  {
    path: '/usuario',
    name: 'usuario',
    component: () => import('../views/Usuario.vue')
  },
  {
    path: '/agregarUsuario',
    name: 'agregarUsuario',
    component: () => import('../views/AgregarUsuario.vue')
  },

  /******************************************************** 
  ************************** CITAS ************************
  *********************************************************/

  {
    path: '/citas',
    name: 'citas',
    component: () => import('../views/Citas.vue')
  },
  {
    path: '/agregarCita',
    name: 'agregarCita',
    component: () => import('../views/AgregarCita.vue')
  },
  {
    path: '/cita/actualizar',
    name: 'citaEdit',
    component: () => import('../views/ActualizarCita.vue')
  },

  /*********************************************************** 
  ************************** TATUAJES ************************
  ************************************************************/

  {
    path: '/agregarTatuaje',
    name: 'agregarTatuaje',
    component: () => import('../views/AgregarTatuaje.vue')
  },
  {
    path: '/editar',
    name: 'editarTatuaje',
    component: () => import('../views/EditarTatuaje.vue')
  }

]

/** Página en modo history */
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
