<template>
  <div id="tatuajes" class="mt-3">
    <div class="d-flex flex-wrap justify-content-evenly align-items-center">
      <div id="for" class="sinFondo">
        <form
          class="form-inline my-2 my-lg-0 d-flex"
          @submit.prevent="buscarTattoo()"
        >
          <input
            id="buscar"
            class="form-control m-1"
            type="search"
            v-model="busqueda"
            placeholder="Buscar"
            aria-label="busqueda"
          />
          <button class="btn btn-outline-success m-1" type="submit">
            Buscar
          </button>
        </form>
        <button class="btn btn-outline-success m-1" @click="play()" id="micro">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            class="bi bi-mic"
            viewBox="0 0 16 16"
          >
            <path
              d="M3.5 6.5A.5.5 0 0 1 4 7v1a4 4 0 0 0 8 0V7a.5.5 0 0 1 1 0v1a5 5 0 0 1-4.5 4.975V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 .5-.5z"
            />
            <path
              d="M10 8a2 2 0 1 1-4 0V3a2 2 0 1 1 4 0v5zM8 0a3 3 0 0 0-3 3v5a3 3 0 0 0 6 0V3a3 3 0 0 0-3-3z"
            />
          </svg>
        </button>
      </div>
      <div id="select" class="d-flex align-items-center m-2">
        <label for="filtro">
          Filtrar busqueda
        </label>
        <select
          name="filtro"
          class="form-control"
          v-model="orden"
          id="enlace"
          @change="cambiarEnlace"
        >
          <option value="">Ninguna</option>
          <option value="all">Todas</option>
          <option value="notatuado">Diseños nuevos</option>
          <option value="tatuado">Diseños antiguos</option>
        </select>
      </div>
      <div id="page" class="d-flex align-items-center m-2">
        <label for="pages" class="m-0">
          Tatuajes
        </label>
        <select v-model="pages" name="pages" class="form-control" id="paginate">
          <option :value="6" selected>6</option>
          <option :value="12">12</option>
          <option :value="18">18</option>
        </select>
      </div>
    </div>
    <b-button
      v-if="accion"
      pill
      class="my-4"
      variant="success"
      size="lg"
      block
      @click="agregarTatuaje()"
    >
      Añadir tatuaje
    </b-button>

    <paginate
      ref="paginator"
      name="tatuajes"
      :list="tatuajes"
      :per="pages"
      class="container d-flex flex-wrap justify-content-center"
    >
      <div
        v-for="(item, index) in paginated('tatuajes')"
        :key="index"
        class="card bg-dark text-white m-2"
        style="width: 288px;"
      >
        <div class="imagen">
          <img
            :src="src + item.imagenes[0]"
            @click="detalles(item._id)"
            class="card-img-top"
            alt="..."
          />
        </div>
        <div class="card-body">
          <ul class="menu">
            <li>
              <span>
                <h5 class="card-title">{{ item.nombre }}</h5>
              </span>
              <span>
                <svg
                  @click="detalles(item._id)"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  class="bi bi-image-fill"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M.002 3a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v10a2 2 0 0 1-2 2h-12a2 2 0 0 1-2-2V3zm1 9v1a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V9.5l-3.777-1.947a.5.5 0 0 0-.577.093l-3.71 3.71-2.66-1.772a.5.5 0 0 0-.63.062L1.002 12zm5-6.5a1.5 1.5 0 1 0-3 0 1.5 1.5 0 0 0 3 0z"
                  />
                </svg>
              </span>
            </li>
          </ul>
          <p
            class="card-text"
            v-if="item.tatuado == false && (orden!='tatuado' && orden!='notatuado')"
          >
            Diseño nuevo
          </p>
          <p
            class="card-text"
            v-else-if="orden != 'tatuado' && orden != 'notatuado'"
          >
            Diseño antiguo
          </p>
          <ul class="menu">
            <li>
              <span>
                <p id="desconocido" class="card-text" v-if="item.autor == ''">
                  Desconocido
                </p>
                <p id="autor" class="card-text" v-else>
                  @ {{ devolverNombre(item.autor) }}
                </p>
              </span>
              <span>
                <a :href="'/usuario?id=' + item.autor" v-if="item.autor != ''">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    fill="currentColor"
                    class="bi bi-person-lines-fill"
                    viewBox="0 0 16 16"
                  >
                    <path
                      d="M6 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm-5 6s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zM11 3.5a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5zm.5 2.5a.5.5 0 0 0 0 1h4a.5.5 0 0 0 0-1h-4zm2 3a.5.5 0 0 0 0 1h2a.5.5 0 0 0 0-1h-2zm0 3a.5.5 0 0 0 0 1h2a.5.5 0 0 0 0-1h-2z"
                    />
                  </svg>
                </a>
                <a v-else>
                  👀
                </a>
              </span>
            </li>
          </ul>
          <div
            class="btn-group"
            role="group"
            aria-label="Basic example"
            v-if="accion"
          >
            <button
              @click="editarTatuaje(item._id)"
              class="btn btn-warning btn-sm"
              title="edit"
            >
              🛠
            </button>
            <button
              @click="showMsgBox(item._id)"
              class="btn btn-danger btn-sm"
              title="delete"
            >
              💥
            </button>
          </div>
        </div>
      </div>
    </paginate>
    <div v-if="tatuajes.length == 0">
      <h2>No se encontraron tatuajes con el siguiente resultado</h2>
    </div>
    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
    <paginate-links
      v-if="tatuajes.length > pages"
      for="tatuajes"
      :show-step-links="true"
      :simple="{
        prev: '◀',
        next: '▶',
      }"
    ></paginate-links>
  </div>
</template>

<script>
export default {
  name: 'Tatuajes',
  data() {
    return {
      pages: 6,
      box: '',
      paginate: ['tatuajes'],
      tatuajes: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      src: 'https://tattoo-backend.vercel.app/',
      tatuaje: {
        nombre: '',
        tatuado: Boolean,
        imagenes: Array(),
        imagen: '',
        autor: '',
      },
      accion: false,
      orden: '',
      busqueda: '',
      nombres: [],
    }
  },
  created() {
    if (localStorage.getItem('tatuador') == 'true') {
      this.accion = true
    }
    var valores = window.location.href.split('?or=')[1]
    if (valores == undefined)
      this.busqueda = window.location.href.split('?se=')[1]
    if (valores != undefined) {
      this.orden = valores.split('&se=')[0]
      this.busqueda = window.location.href.split('se=')[1]
    }
    if (this.busqueda == undefined) this.busqueda = ''
    this.listarTatuajes()
  },
  mounted() {
    this.insertarOpciones(this.orden)
  },
  methods: {
    getImg(item, index) {
      src = item[index].imagenes[0]
      return 'https://tattoo-backend.vercel.app/' + src
    },
    insertarOpciones(valor) {
      var opciones = document.getElementById('enlace')
      if (valor != undefined) opciones.value = valor
      else opciones.value = ''
    },
    buscarTattoo() {
      if (
        this.orden == '' &&
        (this.busqueda == '' || this.busqueda == undefined)
      ) {
        window.location.href = '/'
      } else if (this.orden == '') {
        window.location.href = '?se=' + this.busqueda
      } else if (
        this.orden != '' &&
        (this.busqueda == '' || this.busqueda == undefined)
      ) {
        window.location.href = '?or=' + this.orden
      } else {
        window.location.href = '?or=' + this.orden + '&se=' + this.busqueda
      }
    },
    cambiarEnlace(e) {
      var valor = e.target.value
      if (valor == '' && this.busqueda != '') {
        window.location.href = '?se=' + this.busqueda
      } else if (valor == '' && this.busqueda == '') {
        window.location.href = '/'
      } else if (valor != '' && this.busqueda != '') {
        window.location.href = '?or=' + valor + '&se=' + this.busqueda
      } else {
        window.location.href = '?or=' + valor
      }
    },
    devolverNombre(valor) {
      for (var i = 0; i < this.nombres.length; i++) {
        if (valor == this.nombres[i]._id) {
          return this.nombres[i].nombre
        }
      }
    },
    alerta() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Tatuaje eliminado 💥 '
      this.showAlert()
    },
    agregarTatuaje() {
      window.location.href = '/agregarTatuaje'
    },
    listarTatuajes() {
      this.axios
        .get('/tatuajes/listar')
        .then((res) => {
          this.nombres = res.data.nombres
          var varaux = res.data.tatuajes
          if (this.busqueda == null || this.busqueda == '') {
            if (this.orden == 'all') {
              this.tatuajes = res.data.tatuajes
            } else if (this.orden == 'tatuado') {
              for (var i = 0; i < varaux.length; i++) {
                if (varaux[i].tatuado) this.tatuajes.push(varaux[i])
              }
            } else if (this.orden == 'notatuado') {
              for (var i = 0; i < varaux.length; i++) {
                if (!varaux[i].tatuado) this.tatuajes.push(varaux[i])
              }
            } else {
              varaux.sort(function () {
                return Math.random() - 0.5
              })
              for (var i = 0; i < 12 && i < varaux.length; i++) {
                this.tatuajes.push(varaux[i])
              }
            }
          } else {
            for (var i = varaux.length - 1; i >= 0; i--) {
              if (
                varaux[i].aboutTattoo
                  .toLowerCase()
                  .indexOf(this.busqueda.toLowerCase()) == -1
              ) {
                varaux.splice(i, 1)
              }
            }
            if (this.orden == 'all') {
              this.tatuajes = res.data.tatuajes
            } else if (this.orden == 'tatuado') {
              for (var i = 0; i < varaux.length; i++) {
                if (varaux[i].tatuado) this.tatuajes.push(varaux[i])
              }
            } else if (this.orden == 'notatuado') {
              for (var i = 0; i < varaux.length; i++) {
                if (!varaux[i].tatuado) this.tatuajes.push(varaux[i])
              }
            } else {
              varaux.sort(function () {
                return Math.random() - 0.5
              })
              for (var i = 0; i < 18 && i < varaux.length; i++) {
                this.tatuajes.push(varaux[i])
              }
            }
          }
        })
        .catch((e) => {
          console.log(e.response)
        })
    },
    editarTatuaje(id) {
      window.location.href = '/editar?id=' + id
    },
    eliminarTatuaje(id) {
      const tatuaje = this.tatuajes.find((item) => item._id === id)
      var fotosTatuajes = tatuaje.imagenes
      fotosTatuajes.forEach((nombreTatuaje) => {
        tatuaje.imagen = nombreTatuaje
        this.axios.put('tatuajes/borrarImagen/' + id, tatuaje).then((res) => {})
      })
      this.axios.delete('/tatuajes/borrar/' + id).then((res) => {
        const index = this.tatuajes.findIndex(
          (item) => item._id === res.data._id,
        )
        this.tatuajes.splice(index, 1)
        this.alerta()
      })
    },
    detalles(id) {
      window.location.href = '/tatuaje?id=' + id
    },
    play() {
      document.querySelector('input#buscar').focus()
      var microfono = document.getElementById('micro')
      var microAbierto = this.microAbierto()
      var microCerrado = this.microCerrado()
      const SpeechRecognition = webkitSpeechRecognition
      const recognition = new SpeechRecognition()
      var valor = ''
      recognition.start()

      recognition.onstart = function () {
        document.querySelector('input#buscar').value = 'Escuchando...'
        microfono.innerHTML = microAbierto
      }

      recognition.onspeechend = function () {
        microfono.innerHTML = microCerrado
        recognition.stop()
      }
      recognition.onerror = function (event) {
        document.querySelector('input#buscar').value = ''
      }

      recognition.onresult = function (e) {
        var transcript = e.results[0][0].transcript
        valor = `${transcript}`
        document.querySelector('input#buscar').value = valor
        document.querySelector('input#buscar').nextElementSibling.focus()
      }
      for (var i = 0; i < 10; i++) {
        setTimeout(() => {
          this.busqueda = valor
        }, 500 * i)
      }
    },
    microAbierto() {
      return (
        '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-mic" viewBox="0 0 16 16">' +
        '<path d="M3.5 6.5A.5.5 0 0 1 4 7v1a4 4 0 0 0 8 0V7a.5.5 0 0 1 1 0v1a5 5 0 0 1-4.5 4.975V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 .5-.5z"/>' +
        '<path d="M10 8a2 2 0 1 1-4 0V3a2 2 0 1 1 4 0v5zM8 0a3 3 0 0 0-3 3v5a3 3 0 0 0 6 0V3a3 3 0 0 0-3-3z"/>' +
        '</svg>'
      )
    },
    microCerrado() {
      return (
        '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-mic-fill" viewBox="0 0 16 16">' +
        '<path d="M13 8c0 .564-.094 1.107-.266 1.613l-.814-.814A4.02 4.02 0 0 0 12 8V7a.5.5 0 0 1 1 0v1zm-5 4c.818 0 1.578-.245 2.212-.667l.718.719a4.973 4.973 0 0 1-2.43.923V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 1 0v1a4 4 0 0 0 4 4zm3-9v4.879L5.158 2.037A3.001 3.001 0 0 1 11 3z"/>' +
        '<path d="M9.486 10.607 5 6.12V8a3 3 0 0 0 4.486 2.607zm-7.84-9.253 12 12 .708-.708-12-12-.708.708z"/>' +
        '</svg>'
      )
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    showMsgBox(item) {
      this.boxTwo = ''
      this.$bvModal
        .msgBoxConfirm('¿Quieres eliminarlo?', {
          title: 'Por favor, confirme',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'ELIMINAR',
          cancelTitle: 'CANCELAR',
          footerClass: 'p-2',
          hideHeaderClose: false,
          backgroundColor: 'success',
          centered: true,
        })
        .then((value) => {
          this.box = value
          if (this.box == true) {
            this.eliminarTatuaje(item)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
  },
}
</script>

<style>
form {
  max-width: 700px;
}
#app > div > div > div#for > form > input {
  background-color: #4e647a !important;
  color: #243241;
}
div#for.sinFondo {
  display: flex;
  flex-direction: row;
  align-items: center;
}
#for > button {
  height: 38px;
}
#for > button > svg {
  color: #328b63 !important;
}
#for > button:hover > svg {
  color: #ffffff !important;
}
table {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 10px;
}
a {
  color: #328b63;
  text-decoration: none;
}
div#select > label {
  width: 130px;
  margin-right: 10px;
}
select#enlace,
select#paginate {
  background-color: #4e647a !important;
  color: #243241 !important;
  width: 140px !important;
}
select > option:hover {
  background-color: #243241 !important;
  color: #82a5c7 !important;
}
div#page > label {
  width: 100px;
  margin-right: 10px;
}
div#page select {
  width: 45px !important;
}

/* Estilo card */

#app
  > div.mt-3
  > ul.container.d-flex.flex-wrap.justify-content-center
  > div:hover {
  border: 1px solid #328b63;
}

h5.card-title:hover {
  cursor: pointer;
}
h5.card-title {
  font-weight: 600;
}
p.card-text,
h5 {
  margin: 0;
  color: #82a5c7;
}
div#tatuajes div.imagen {
  width: 286px;
  height: 200px;
  object-fit: none;
}
div#tatuajes div.imagen img {
  width: 286px;
  height: 200px;
  object-fit: cover;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
}
img:hover {
  cursor: pointer;
}
#app > div > div.container.d-flex.flex-wrap > div > div.card-body {
  width: 288px;
}
#app > div.mt-3 > ul.container.d-flex.flex-wrap.justify-content-center > div {
  border-radius: 20px;
}
.menu {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}
.menu li {
  height: 40px;
}
div.card-body {
  padding-top: 5px;
}
.menu li span:first-child {
  display: inline-block;
  padding: 10px;
}
.menu li {
  display: block;
  position: relative;
  overflow: hidden;
}
.menu li span:last-child {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-100%);
}
.menu li span {
  transition: transform 0.2s ease-out;
}
.menu li:hover span:first-child {
  transform: translateY(100%);
}
.menu li:hover span:last-child {
  transform: none;
  cursor: pointer;
}
.menu[data-animation='to-top'] a span:last-child {
  transform: translateY(100%);
}

/* Estilo texto card */

li span p {
  background: linear-gradient(
    90deg,
    rgba(232, 255, 0, 1) 0%,
    rgba(23, 212, 60, 1) 50%,
    rgba(0, 176, 255, 1) 100%
  );
  font-weight: 600;
  inset: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
li span p#desconocido {
  background: linear-gradient(
    90deg,
    rgba(255, 0, 0, 1) 0%,
    rgba(221, 29, 207, 1) 50%,
    rgba(255, 0, 0, 1) 100%
  );
  font-weight: 600;
  inset: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
svg {
  color: rgb(200, 255, 0) !important;
}

/* Paginacion */

.paginate-links {
  width: 100%;
  list-style: none;
  text-align: center;
  margin-top: 70px;
}
.paginate-links li {
  width: 70px;
  display: inline;
  background-color: #82a5c7;
  color: #243241;
  padding: 10px;
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  border-radius: 3px;
}
.paginate-links li:hover {
  color: rgb(200, 255, 0) !important;
  cursor: pointer;
}
.paginate-result {
  width: 100%;
  text-align: center;
  margin-bottom: 1rem;
}
</style>
