<script>
export default {
  data() {
    return {
      inicio: false,
      acceso: false,
      usuario: '',
      link: '',
    }
  },
  created() {
    if (localStorage.getItem('id') != null) {
      this.inicio = true
      this.usuario = localStorage.getItem('usuario')
    }
    if (localStorage.getItem('tatuador') == 'true') {
      this.acceso = true
    }
  },
  methods: {
    cerrarSesion() {
      localStorage.clear()
      window.location.href = '/'
    },
    perfilUsuario() {
      window.location.href = '/usuario?id=' + localStorage.getItem('id')
    },
  },
}
</script>

